import React, { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Coins, Plus, AlertTriangle, Infinity, BarChart3 } from 'lucide-react';
import { toast } from 'sonner';
import axiosInstance from '@/api/axios/axiosInstance';
import PurchaseCreditsModal from '@/components/Credits/PurchaseCreditsModal';
import CreditUsageModal from '@/components/Credits/CreditUsageModal';
import { useAuth } from '@/context/AuthContext';

const EnhancedHeaderCreditBalance = () => {
  const { user } = useAuth();
  const [balance, setBalance] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [showPurchaseModal, setShowPurchaseModal] = useState(false);
  const [showUsageModal, setShowUsageModal] = useState(false);

  const fetchBalance = async () => {
    try {
      setLoading(true);
      const response = await axiosInstance.get(`/api/credits/balance?t=${Date.now()}`);
      const actualBalance = response.data.data;
      setBalance(actualBalance);
      setError(null);
    } catch (err) {
      console.error('Error fetching credit balance:', err);
      setError(err.response?.data?.message || 'Failed to fetch credit balance');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchBalance();

    // Listen for credit balance updates
    const handleCreditUpdate = () => {
      fetchBalance();
    };

    window.addEventListener('creditBalanceUpdate', handleCreditUpdate);
    window.addEventListener('storage', handleCreditUpdate);

    return () => {
      window.removeEventListener('creditBalanceUpdate', handleCreditUpdate);
      window.removeEventListener('storage', handleCreditUpdate);
    };
  }, []);

  const handlePurchaseCredits = () => {
    setShowPurchaseModal(true);
  };

  const handlePurchaseSuccess = () => {
    toast.success('ðŸŽ‰ Purchase successful! Credits added to your account.');
    setShowPurchaseModal(false);
    fetchBalance();
    window.dispatchEvent(new CustomEvent('creditBalanceUpdate'));
  };

  const formatCredits = (credits) => {
    if (credits === 'unlimited') return 'Unlimited';
    const numCredits = typeof credits === 'string' ? parseFloat(credits) : credits;
    if (typeof numCredits === 'number' && !isNaN(numCredits)) {
      return numCredits.toLocaleString('en-US', {
        minimumFractionDigits: 0,
        maximumFractionDigits: 0
      });
    }
    return '0';
  };

  const getUserInitials = () => {
    if (user?.first_name && user?.last_name) {
      return `${user.first_name.charAt(0)}${user.last_name.charAt(0)}`.toUpperCase();
    } else if (user?.name) {
      const names = user.name.split(' ');
      if (names.length >= 2) {
        return `${names[0].charAt(0)}${names[1].charAt(0)}`.toUpperCase();
      }
      return user.name.charAt(0).toUpperCase();
    } else if (user?.username) {
      return user.username.charAt(0).toUpperCase();
    } else if (user?.email) {
      return user.email.charAt(0).toUpperCase();
    }
    return 'U';
  };

  const getUserDisplayName = () => {
    if (user?.first_name && user?.last_name) {
      return `${user.first_name} ${user.last_name}`.toUpperCase();
    } else if (user?.name) {
      return user.name.toUpperCase();
    } else if (user?.username) {
      return user.username.toUpperCase();
    } else if (user?.email) {
      return user.email.split('@')[0].toUpperCase();
    }
    return 'USER';
  };

  if (loading) {
    return (
      <div className="flex items-center space-x-4">
        <div className="h-8 bg-gray-200 rounded w-32 animate-pulse"></div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="flex items-center space-x-2 text-red-600">
        <AlertTriangle className="h-4 w-4" />
        <span className="text-sm">Error loading balance</span>
      </div>
    );
  }

  return (
    <>
      <div className="flex items-center space-x-4">
        {/* Credits Left Section - Orange Design */}
        <div className="flex items-center w-[150px] bg-gradient-to-r from-orange-400 to-orange-500 rounded-lg px-4 py-2 text-white shadow-sm">
          <div className="flex items-center space-x-2">
            <Coins className="h-4 w-4 text-white" />
            <div className="flex flex-col">
              <span className="text-xs font-medium opacity-90">Credits Left</span>
              <span className="text-sm font-bold">
                {balance?.isSuperAdmin ? (
                  <div className="flex item-between space-x-1">
                    <Infinity className="h-3 w-3" />
                    <span className="text-xs">Unlimited</span>
                  </div>
                ) : (
                  formatCredits(balance?.available_credits)
                )}
              </span>
            </div>
          </div>
          
          {/* Recharge Button */}
          {!balance?.isSuperAdmin && (
            <Button
              size="sm"
              onClick={handlePurchaseCredits}
              className="ml-3 bg-red-500 hover:bg-red-600 text-white text-xs px-3 py-1 h-6 rounded-md font-medium"
            >
              <Plus className="h-3 w-3 mr-1" />
              Recharge
            </Button>
          )}
          
          {/* Lifetime Access Badge */}
          
        </div>

        {/* Notification Bell */}
        {/* <div className="relative">
          <Bell className="w-5 h-5 cursor-pointer text-gray-600 hover:text-gray-800 transition-colors" />
          <span className="absolute -top-1 -right-1 h-3 w-3 rounded-full bg-red-500 text-[8px] flex items-center justify-center text-white font-bold">
            3
          </span>
        </div> */}

        {/* Help Button */}
        {/* <Button variant="ghost" size="sm" className="text-gray-600 hover:text-gray-800">
          <HelpCircle className="w-4 h-4 mr-1" />
          Help
        </Button> */}

        {/* Credits Usage Button */}
        <Button
          variant="outline"
          size="sm"
          onClick={() => setShowUsageModal(true)}
          className="text-gray-700 bg-orange-500 "
        >
          <BarChart3 className="w-4 h-4 mr-1" />
          Credits Usage
          
        </Button>

        {/* User Profile */}
        <div className="flex items-center space-x-2">
          <div className="w-8 h-8 bg-blue-500 rounded-full flex items-center justify-center text-white text-sm font-medium">
            {getUserInitials()}
          </div>
          <span className="text-sm font-medium text-gray-700 hidden md:block">
            {getUserDisplayName()}
          </span>
        </div>
      </div>

      {/* Purchase Credits Modal */}
      <PurchaseCreditsModal
        isOpen={showPurchaseModal}
        onClose={() => setShowPurchaseModal(false)}
        onSuccess={handlePurchaseSuccess}
      />

      {/* Credit Usage Modal */}
      <CreditUsageModal
        isOpen={showUsageModal}
        onClose={() => setShowUsageModal(false)}
      />
    </>
  );
};

export default EnhancedHeaderCreditBalance;
