import { Outlet } from "react-router-dom";
import { useState } from "react";
import DynamicSidebar from "@/components/DynamicSidebar";
import Topbar from "./Topbar";
import SettingsIcon from "./SettingsIcon";
import { useMobileMenu } from "@/context/MobileMenuContext";

const DashboardLayout = () => {
  const { isMobileView } = useMobileMenu();
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false);

  const toggleSidebar = () => {
    setSidebarCollapsed(!sidebarCollapsed);
  };

  return (
    <main
      className="h-screen flex bg-background text-foreground overflow-hidden"
      id="application-theme"
    >
      <div className="absolute inset-0 z-0 opacity-30 pointer-events-none">
        <div className="absolute inset-0 bg-gradient-to-br from-primary/5 to-transparent"></div>
        <div className="grid grid-cols-[repeat(20,1fr)] grid-rows-[repeat(20,1fr)] h-full w-full">
          {Array.from({ length: 20 }).map((_, i) => (
            <div
              key={`grid-line-h-${i}`}
              className="border-t border-primary/[0.03]"
            ></div>
          ))}
          {Array.from({ length: 20 }).map((_, i) => (
            <div
              key={`grid-line-v-${i}`}
              className="border-l border-primary/[0.03]"
            ></div>
          ))}
        </div>
      </div>

      {/* Dynamic Sidebar with role-based permissions */}
      <DynamicSidebar isCollapsed={sidebarCollapsed} onToggle={toggleSidebar} />

      <section
        className="flex flex-col justify-start flex-1 relative z-10 min-w-0 transition-all duration-300 ease-in-out"
        style={!isMobileView ? {
          marginLeft: sidebarCollapsed ? '80px' : '280px'
        } : undefined}
      >
        <Topbar />
        <div className="flex-1 overflow-y-auto overflow-x-hidden">
          <Outlet />
        </div>
        {/* {!isMobileView && <SettingsIcon />} */}
      </section>
    </main>
  );
};

export default DashboardLayout;
