import React, { useState } from "react";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { BellIcon, LogOut, Settings, User } from "lucide-react";
import { ModeToggle } from "./mode-toggle";
import { useNavigate } from "react-router-dom";
import { useAuth } from "@/context/AuthContext";


interface ProfileMenuProps {
  showFullMenu?: boolean;
}

const ProfileMenu: React.FC<ProfileMenuProps> = ({ showFullMenu = false }) => {
  const navigate = useNavigate();
  const { logout, user } = useAuth();
  const handleLogout = () => {
    logout();
    navigate("/login");
  };

  // Generate user initials
  const getUserInitials = () => {
    if (user?.first_name && user?.last_name) {
      return `${user.first_name.charAt(0)}${user.last_name.charAt(0)}`.toUpperCase();
    } else if (user?.name) {
      const names = user.name.split(' ');
      if (names.length >= 2) {
        return `${names[0].charAt(0)}${names[1].charAt(0)}`.toUpperCase();
      }
      return user.name.charAt(0).toUpperCase();
    } else if (user?.username) {
      return user.username.charAt(0).toUpperCase();
    } else if (user?.email) {
      return user.email.charAt(0).toUpperCase();
    }
    return 'U';
  };

  // Get display name
  const getDisplayName = () => {
    if (user?.first_name && user?.last_name) {
      return `${user.first_name} ${user.last_name}`;
    } else if (user?.name) {
      return user.name;
    } else if (user?.username) {
      return user.username;
    } else if (user?.email) {
      return user.email.split('@')[0]; // Show email username part
    }
    return 'User';
  };

  return (
    <DropdownMenu>
      <DropdownMenuTrigger asChild>
        <div className="flex items-center space-x-3 cursor-pointer p-2 rounded-full ">
          <div className="h-9 w-9 rounded-full bg-gradient-to-r from-orange-400 to-orange-500 flex items-center justify-center text-white font-bold text-sm">
            {getUserInitials()}
          </div>
          {showFullMenu && <span className="text-sm font-medium">{getDisplayName()}</span>}
        </div>
      </DropdownMenuTrigger>
      <DropdownMenuContent align="end" className="w-56">
        <DropdownMenuLabel>My Account</DropdownMenuLabel>
        <DropdownMenuSeparator />
        
        {/* Only show these items on mobile */}
        {!showFullMenu && (
          <>
            <DropdownMenuItem className="flex items-center gap-2">
              <div className="relative">
                <BellIcon className="w-4 h-4" />
                <span className="absolute -top-1 -right-1 h-3 w-3 rounded-full bg-primary text-[8px] flex items-center justify-center text-white font-bold">
                  3
                </span>
              </div>
              <span>Notifications</span>
            </DropdownMenuItem>
            
            <DropdownMenuItem className="flex items-center gap-2">
              <ModeToggle showLabel={true} />
            </DropdownMenuItem>
            
            <DropdownMenuSeparator />
          </>
        )}
        
        <DropdownMenuItem className="flex items-center gap-2" onClick={() => navigate("/profile")}>
          <User className="w-4 h-4" />
          <span>Profile</span>
        </DropdownMenuItem>
        
        <DropdownMenuItem className="flex items-center gap-2" onClick={() => navigate("/settings")}>
          <Settings className="w-4 h-4" />
          <span>Settings</span>
        </DropdownMenuItem>
        
        <DropdownMenuSeparator />
        
        <DropdownMenuItem className="flex items-center gap-2 text-red-500" onClick={handleLogout}>
          <LogOut className="w-4 h-4" />
          <span>Logout</span>
        </DropdownMenuItem>
      </DropdownMenuContent>
    </DropdownMenu>
  );
};

export default ProfileMenu;
