// Function to get icon by name, with fallback
export const getIconByName = (iconName: string | null): React.ReactNode => {
  if (!iconName) return <FileText />; // Default fallback icon
  
  // Try exact match first
  if (iconMap[iconName]) {
    return iconMap[iconName];
  }
  
  // Try case-insensitive match
  const lowerIconName = iconName.toLowerCase();
  const matchingKey = Object.keys(iconMap).find(key => key.toLowerCase() === lowerIconName);
  if (matchingKey) {
    return iconMap[matchingKey];
  }
  
  // Fallback to default icon
  return <FileText />;
};

export default getIconByName;
