import React, { useState, useRef, useEffect } from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { motion } from 'framer-motion';
import { X, ChevronLeft, ChevronRight, LogOut } from 'lucide-react';
import { gsap } from 'gsap';
import { cn } from '@/lib/utils';
import { useAuth } from '@/context/AuthContext';
import { usePermissions } from '@/context/PermissionContext';
import { useMobileMenu } from '@/context/MobileMenuContext';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from '@/components/ui/tooltip';
import { getIconByName } from './DynamicSidebarGenerator';
import { FireIcon as HeroFireIcon } from '@heroicons/react/24/solid';
import LogoutModal from '@/components/LogoutModal';

/* ================= ICON ================= */

const AnimatedFireIcon = React.forwardRef<HTMLDivElement, any>(
  ({ className, isAnimated = false }, ref) => (
    <motion.div
      ref={ref}
      animate={isAnimated ? { scale: [1, 1.1, 1], rotate: [0, 5, -5, 0] } : {}}
      transition={{ duration: 2, repeat: isAnimated ? Infinity : 0 }}
      className={className}
    >
      <HeroFireIcon className="w-full h-full text-orange-500" />
    </motion.div>
  )
);
AnimatedFireIcon.displayName = 'AnimatedFireIcon';

/* ================= SIDEBAR ITEM ================= */

const DynamicSidebarItem = ({
  item,
  isCollapsed,
  isActive,
  onClick,
}: any) => {
  const content = (
    <div
      className={cn(
        "flex items-center cursor-pointer neo-sidebar-item transition-all duration-200 relative overflow-hidden",
        isCollapsed ? "p-3 justify-center" : "px-3 py-3 space-x-4",
        isActive && "neo-sidebar-item-active"
      )}
    >
      <div className="w-6 h-6 flex items-center justify-center">
        {item.icon}
      </div>
      {!isCollapsed && (
        <span className="text-sm font-medium">{item.label}</span>
      )}
    </div>
  );

  return (
    <TooltipProvider>
      <Tooltip>
        <TooltipTrigger asChild>
          {onClick ? (
            <button onClick={onClick} className="w-full">
              {content}
            </button>
          ) : (
            <Link to={item.href} className="w-full text-white block">
              {content}
            </Link>
          )}
        </TooltipTrigger>
        {isCollapsed && (
          <TooltipContent side="right">
            {item.label}
          </TooltipContent>
        )}
      </Tooltip>
    </TooltipProvider>
  );
};

/* ================= MAIN SIDEBAR ================= */

const DynamicSidebar = ({ isCollapsed, onToggle }: any) => {
  const navigate = useNavigate();
  const location = useLocation();
  const { logout } = useAuth();
  const permissionContext = usePermissions();
  const { isMobileMenuOpen, isMobileView, closeMobileMenu } = useMobileMenu();
  const [showLogoutModal, setShowLogoutModal] = useState(false);

  const toggleButtonRef = useRef<HTMLButtonElement>(null);
  const logoRef = useRef<HTMLDivElement>(null);

  const generateDynamicSidebarItems = () => {
    const { pagePermissions, isSuperAdmin } = permissionContext;
    if (!pagePermissions) return [];

    return pagePermissions
      .filter((p: any) => isSuperAdmin || p.can_view)
      .map((p: any) => ({
        id: p.page_path,
        icon: getIconByName(p.icon),
        label: p.page_name,
        href: p.page_path,
        category: p.page_category,
        sortOrder: p.sort_order || 0,
      }))
      .sort((a: any, b: any) => a.sortOrder - b.sortOrder);
  };

  const items = generateDynamicSidebarItems();
  const grouped = items.reduce((acc: any, item: any) => {
    acc[item.category || 'main'] ||= [];
    acc[item.category || 'main'].push(item);
    return acc;
  }, {});

  const isRouteActive = (path: string) =>
    location.pathname === path || location.pathname.startsWith(path);

  useEffect(() => {
    if (toggleButtonRef.current && logoRef.current && !isMobileView) {
      gsap.to(logoRef.current, { scale: isCollapsed ? 0.8 : 1, duration: 0.3 });
      gsap.to(toggleButtonRef.current, {
        right: isCollapsed ? -16 : -32,
        duration: 0.3,
      });
    }
  }, [isCollapsed, isMobileView]);

  return (
    <>
      <div
        className={cn(
          "fixed left-0 top-0 h-full neo-sidebar z-50 flex flex-col transition-all",
          isMobileView
            ? isMobileMenuOpen ? "translate-x-0 w-80" : "-translate-x-full w-80"
            : "translate-x-0"
        )}
        style={!isMobileView ? { width: isCollapsed ? 80 : 280 } : undefined}
      >
        {isMobileView && (
          <button onClick={closeMobileMenu} className="absolute top-4 right-4">
            <X />
          </button>
        )}

        {/* LOGO */}
        <div className="flex items-center justify-center py-6">
          <AnimatedFireIcon
            ref={logoRef}
            className="w-8 h-8"
            isAnimated={location.pathname === "/"}
          />
          {!isCollapsed && <span className="ml-3  text-white font-bold">AI CRUITMENT</span>}
        </div>

        {!isMobileView && (
          <button
            ref={toggleButtonRef}
            onClick={onToggle}
            className="neo-sidebar-toggle h-8 w-8 rounded-full absolute top-1/2 -right-3 flex items-center justify-center text-white"
          >
            {isCollapsed ? <ChevronRight /> : <ChevronLeft />}
          </button>
        )}

        {/* NAV */}
        <ScrollArea className="flex-1 px-4">
          <nav className="space-y-2">
            {Object.entries(grouped).map(([cat, list]: any) => (
              <div key={cat}>
                {!isCollapsed && cat !== 'main' && (
                  <div className="neo-sidebar-category text-xs uppercase px-2 py-2">
                    {cat}
                  </div>
                )}
                {list.map((item: any) => (
                  <DynamicSidebarItem
                    key={item.id}
                    item={item}
                    isCollapsed={isCollapsed && !isMobileView}
                    isActive={isRouteActive(item.href)}
                    onClick={
                      isMobileView
                        ? () => {
                            navigate(item.href);
                            closeMobileMenu();
                          }
                        : undefined
                    }
                  />
                ))}
              </div>
            ))}
          </nav>
        </ScrollArea>

        {/* LOGOUT */}
        <div className="p-4">
          <button
            onClick={() => setShowLogoutModal(true)}
            className="neo-sidebar-logout w-full py-3 rounded-md text-white flex items-center justify-center gap-2"
          >
            <LogOut size={16} />
            {!isCollapsed && "Logout"}
          </button>
        </div>
      </div>

      <LogoutModal
        isOpen={showLogoutModal}
        onClose={() => setShowLogoutModal(false)}
        onConfirm={() => logout()}
      />
    </>
  );
};

export default DynamicSidebar;



