// src/layout/Dashboard/Topbar.tsx
import { FC } from "react";
import { LayoutDashboard, Menu } from "lucide-react";
import { Link } from "react-router-dom";
import Breadcrumbs from "@/components/Breadcrumbs";
import { useMobileMenu } from "@/context/MobileMenuContext";
import ProfileMenu from "@/components/ProfileMenu";
import HeaderCreditBalance from "@/components/Layout/HeaderCreditBalance";
import EnhancedHeaderCreditBalance from "@/components/Layout/EnhancedHeaderCreditBalance";
import { cn } from "@/lib/utils";

const Topbar: FC = () => {
  const { toggleMobileMenu, isMobileView } = useMobileMenu();

  return (
    <div
      className="
        h-16
        flex items-center justify-between
        px-4 md:px-6
        z-10
        relative
        overflow-hidden

        /* ðŸ”¥ DARK NEON BACKGROUND */
        bg-gradient-to-r
        from-[#0b0f2e]
        via-[#0e1340]
        to-[#0b0f2e]

        border-b border-white/10
        backdrop-blur-md
        text-white
         [&_*]:text-white
      "
    >
      {/* Neon glow layer â€“ visual only */}
      <div
        aria-hidden
        className="
          absolute inset-0 pointer-events-none
          bg-gradient-to-r
          from-indigo-500/20
          via-purple-500/15
          to-pink-500/20
        "
      />

      {/* LEFT */}
      <div className="relative flex items-center space-x-2 md:space-x-6 flex-1 min-w-0">
        {/* Mobile menu */}
        {isMobileView && (
          <button
            onClick={toggleMobileMenu}
            className="p-2 rounded-md hover:bg-white/10 transition-colors"
            aria-label="Toggle mobile menu"
          >
            <Menu className="h-5 w-5 text-white" />
          </button>
        )}

        {/* Dashboard link */}
        <Link to="/" className="flex-shrink-0">
          <h2 className="flex items-center gap-2 text-lg font-bold px-3 py-1 rounded-md bg-white/10 text-white">
            <LayoutDashboard className="h-4 w-4 text-white" />
            <span className="hidden xs:inline">Dashboard</span>
          </h2>
        </Link>

        {/* Breadcrumbs (UNCHANGED POSITIONING) */}
        <div
          className={cn(
            "min-w-0 flex-1 max-w-xl",
            isMobileView ? "hidden" : "block"
          )}
        >
          <Breadcrumbs className="text-xs sm:text-sm text-white" />
        </div>
      </div>

      {/* RIGHT */}
      <div className="relative flex items-center space-x-4 md:space-x-6">
        {!isMobileView && <EnhancedHeaderCreditBalance />}
        {isMobileView && (
          <>
            <HeaderCreditBalance />
            <ProfileMenu showFullMenu={false} />
          </>
        )}
      </div>
    </div>
  );
};

export default Topbar;



